@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&family=Open+Sans:wght@300;400;600;700;800&display=swap');

$font-body: 'Open Sans', Verdana, Helvetica, Arial, sans-serif;
$font-button: 'Amatic SC', 'Open Sans', Verdana, Helvetica, sans-serif;

h1 {
  font-size: 3rem;
  font-weight: 600;
  letter-spacing: -1px;
  line-height: 1.1;
  color: $color-tertiary;

  @media screen and (min-width: 48em) {
    font-size: 5rem;
  }
}

h2 {
  font-size: 2rem;
  font-weight: 600;
}

h3 {
  margin: 0;
  font-size: 1.5rem;
  font-weight: 600;
}

h4, h5 {
  line-height: 1.6;
}

p {
  font-weight: 400;
  line-height: 1.6;
  margin-bottom: 0;
}

button {
  font-size: 1.5rem;
  font-weight: 700;
}

figcaption {
  font-size: 1.2rem;
  font-weight: 300;
  text-align: center;
}
