.logo {
  position: relative;
  bottom: -8px;
  height: 60px;

  @media screen and (min-width: 48em) {
    height: 80px;
  }
}

.image {
  &__header {
    &--background {
      background-image: url("/assets/images/headerfoto-Schuytse-Tandartsen-Arnhem.jpg");
      background-position: center center;
      background-size: cover;
      overflow: hidden;
    }
  }

  &__container--home  {
    height: 100%;

    img {
      height: 100%;
    }
  }

  &--background {
    background-image: url("/assets/images/divider-tandarts-kind-Schuytse-Tandartsen-Arnhem.jpg");
    background-position: center center;
    background-size: cover;
    height: 400px;
  }

  &--index {
    position: absolute;
    top: 5rem;
    right: -12rem;
    height: 44rem;
  }

  &--usp {
    @media screen and (min-width: 48em) {
      height: 14rem !important;
    }
  }
}

svg path {
  position: relative;
  top: -1rem;
  z-index: 99;
  stroke: none;

  &.grey {
    fill: $color-tertiary;
  }

  &.white {
    fill: $color-white;
  }
}
