nav {
  position: relative;
  background-color: $color-white;
  letter-spacing: 0;
  line-height: 1.2;
  z-index: 9999;

  ul {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    margin: 0;
    padding: 1rem 1rem .5rem;
    list-style: none;

    @media screen and (min-width: 48em) {
      padding: 2rem 2rem .5rem;
    }

    li.top {
      color: $color-black;
      position: relative;
      padding: 0 1rem;

      ul.children {
        display: none;
        flex-direction: column;
        padding: 0;
        font-size: 14px;
        line-height: 1.3;
        background-color: $color-white;
      }

      &:hover {
        ul.children {
        display: flex;
        position: absolute;
        top: 1rem;
        left: 0;
        z-index: 99;
        padding-top: 1.5rem;
        }

        > a, > a:visited {
          color: $color-primary;
        }
      }

      a, a:visited {
        color: $color-black;
        text-decoration: none;
      }

      // Highlight the a and the active child of an active li.top
      &.active {
        > a, li.child.active {
         background-color: $color-tertiary;
        }
      }

      li.child {
        padding: .5rem 1rem;

        &:first-child {
          border-top: 3px solid $color-black;
        }

        &:hover {
          background-color: lightgrey;
        }
      }
    }
  }
}
