form {
  fieldset {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    padding: 2em;
    background-color: $color-tertiary;
    border: none;
  }

  label {
    margin-bottom: .2em;
  }

  input, textarea {
    width: 85%;
    font-size: 1em;
    min-height: 2.5rem;
    margin-bottom: 1em;
    padding: 0 2.5em 0 .5em;
    border: none;
    border-radius: 0;
    font-family: inherit;
  }

  section {
    display: flex;
    flex-direction: row;
  }

  p {
    display: flex;
    flex-direction: row;
    align-items: center;

    &:last-child {
      padding-left: 1em;
    }

    input {
      margin: 0;
      width: 1.5rem;
    }

    label {
      padding-left: .5em;
    }
  }
}
