// FLEX HELPERS
.flex {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  @media screen and (min-width: 48em) {
    flex-direction: row;
    flex-wrap: nowrap;
  }
}

.flex-column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.space-between {
  justify-content: space-between;
}

.space-around {
  justify-content: space-around;
}

// SPACING HELPERS

.w-100 {
  width: 100%;
}

.w-50 {
  width: 50%;
}

.h-100 {
  width: 100%;

  @media screen and (min-width: 48em) {
    width: auto;
    height: 100%;
  }
}

.h-50 {
  height: 50%;
}

.m-0 {
  margin: 0 !important;

  @media screen and (max-width: 767px) {
    &-xs {
      margin: 0 !important;
    }
  }
}

.m-1 {
  margin: .5rem !important;

  @media screen and (min-width: 48em) {
    margin: 1rem !important;
  }
}

.m-2 {
  margin: 1rem !important;

  @media screen and (min-width: 48em) {
    margin: 2rem !important;
  }
}

.m-3 {
